import wx from 'weixin-js-sdk'
import axios from  'axios'
const Common={
  Url:'http://203.195.218.83/vote',
  //
  shareTitle : "",
  shareDesc : "",
  shareImgUrl :"http://www.weirdor.cn/voteing/logo.jpg",
  currentUrl:'http://www.weirdor.cn/voteing',
  appid:'wxf2d01f227c9714f1',
  appKen:'Y2R3bGppdXll',
  //
  GetRequest:function(name){
     var reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)"); //
    var r = window.location.search.substr(1).match(reg);  //
    if (r!=null) return unescape(r[2]); return null; //
  },
  Toast(that,ToastIcon,ToastText,fn){
    that.setState({
      ToastIcon:ToastIcon,
      ToastText:ToastText,
    });
    if(fn){
      fn();
    }
  },
  shareSuccessFn : function (){
    //alert('');
  },
  shareCancelFn : function (){
    //alert('');
  },
  init : function(callbackFn){
    var _this=this;
    if(undefined != callbackFn && callbackFn != null){
      this.initCallback = callbackFn;
    }
    var origUrl = window.location.href.split('#')[0];
    var url = encodeURIComponent(origUrl);
    axios({
      method: 'get',
      url:_this.Url+'/wechat/portal/signa?url='+url
    }).then((request)=>{
        if(request.status=='200'){
          var request=request.data;
          _this.appid=request.appid
          wx.config({
                debug: false,
                appId: request.appid,
                timestamp: request.timestamp,
                nonceStr: request.nonceStr,
                signature: request.signature,
                jsApiList: [
                         "onMenuShareTimeline",
                         "onMenuShareAppMessage"
                          ]
            });
            _this.initWxApi();
        }
    }).catch((request)=>{
      console.log(''+request);
    })
  },
  //
  navigor:function(){
		var u = navigator.userAgent;
		if(u.toLowerCase().match(/MicroMessenger/i) == 'micromessenger'){
			return 2;
		}
		else{
			if(u.indexOf('Android') > -1 || u.indexOf('Adr') > -1){
				return 0;
			}
			else if(!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)){
				return 1;
			}
		}
	},
  getUrlParam:function(name){
		var reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)"); //
		var r = window.location.search.substr(1).match(reg);  //
		if (r!=null) return unescape(r[2]); return null; //

	},
  initWxApi:function(){
      var _this=this;
      wx.error(function(res){
      });
      wx.ready(function(){
          wx.onMenuShareTimeline({
              title: _this.shareTitle, // 
              imgUrl:  _this.shareImgUrl, // 
              link:_this.currentUrl,
              success: function () {
                  _this.shareSuccessFn();
              },
              cancel: function () {
                _this.shareCancelFn();
              }
          });
          wx.onMenuShareAppMessage({
                title:  _this.shareTitle, // 
                desc: _this.shareDesc, // 
                imgUrl: _this.shareImgUrl, // 
                link:_this.currentUrl,
                success: function () {
                  _this.shareSuccessFn();
                },
                cancel: function () {
                  _this.shareCancelFn();
                }
            });
        });
  },
  Replace(str,new_str){
  	str = str.replace(/(\n)/g,new_str);
  	return str;
  },

}

export default Common



